<template>
  <div class="color-thief" ref="colorThiefBox">
    <div class="img-box">
      <div class="img-item" v-for="(item, index) in imgList" :key="index">
        <img
          :src="item"
          alt=""
          @mouseenter="handleEnter($event, index)"
          @mouseleave="handleLeave"
          :style="{
            opacity: handleIndex === -1 ? 1 : index === handleIndex ? 1 : 0.3,
            transition: '0.5s',
          }"
        />
      </div>
    </div>
  </div>
</template>
<script setup>
import { ref } from "vue";
// const imgList = ref([
//   "https://img2.baidu.com/it/u=458229905,4078405655&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=281",
//   "https://img0.baidu.com/it/u=3225345106,2528108771&fm=253&fmt=auto&app=138&f=JPEG?w=889&h=500",
//   "https://img2.baidu.com/it/u=320070717,1294000015&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=312",
//   "https://img0.baidu.com/it/u=1117529929,3665861798&fm=253&fmt=auto&app=138&f=JPEG?w=800&h=500",
// ]);
const imgList = ref([]);
let list = require.context("@/assets/img/colorThiefList", true, /\.png$/);
let keys = list.keys();
for (let index = 0; index < keys.length; index++) {
  const value = list(keys[index]);
  console.log("列表数据", value);
  imgList.value.push(value);
}
const handleIndex = ref(-1);
const colorThiefBox = ref(null);
import ColorThief from "colorthief";
const colorThief = new ColorThief();
function handleEnter(e, i) {
  handleIndex.value = i;
  let colors = colorThief.getPalette(e.target, 3);
  let colorArr = colors.map((c) => {
    return `rgb(${c[0]},${c[1]},${c[2]})`;
  });
  colorThiefBox.value.style.setProperty(
    "background",
    `linear-gradient(to bottom right , ${colorArr[0]},${colorArr[1]},${colorArr[2]})`
  );
  console.log(colorThiefBox.value.style);
}
function handleLeave(e) {
  handleIndex.value = -1;
  colorThiefBox.value.style.setProperty("background", "#fff");
}
</script>
<style lang="scss" scope>
.color-thief {
  width: 100%;
  height: 100%;
  .img-box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 1200px;
    flex-wrap: wrap;
    margin: 0 auto;
    padding-top: 40px;

    .img-item {
      flex: 49%;
      margin-right: 1%;
      margin-bottom: 1%;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
</style>